.onePx_bottom {
  position: relative; }
  .onePx_bottom:after {
    content: '';
    width: 200%;
    background: #ededed;
    height: 1px;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: scale(0.5);
    transform-origin: 0; }

